#loginCard{
  width: 500px;
  margin: 0 auto;
  margin-top: 50px;

  #tittle{
      margin: 0 auto;
      margin-top: 20px;
      margin-bottom: 20px;
      width: 400px;

      h1{
          margin: 0 auto;
          margin-top: 20px;
          margin-bottom: 20px;
      }
  }

  #logoImg{
      padding-top: 40%; // 16:9,
      //margin: 0 auto;
      height: 30px;
      border-radius: 10px;
  }

  #textFieldArea{
      width: 100%;
      display: block;
      text-align: center;

      .textField{
          width: 80%;
          margin-bottom: 20px;
      }
  }

  .loginBtn{
      display: inline-block;
      margin: 0 auto;
      margin-top: 20px;
      margin-right: 20px;
      margin-bottom: 20px;
      background-color: #2E86C1;
      border-color: #2E86C1;
      width: 120px;
      vertical-align: middle;
  }
}

.warnMsg{
  width: 650px;
  height: 50px;
  background-color: #FADBD8;
  vertical-align: middle;
  margin: 0 atuo;
  text-align: center;
  display: table-cell;
  vertical-align: middle;
  font: bold;
  margin: 0px;
}

html, body {
    height: 100%;
    background-color: #f0f3f9;
}

#mainGrid{
    height: calc( 100vh - 50px );
    padding: 0;
    width: 100vw;
    
    .messagePanel-background{
        height: calc( 100vh - 103px );
        color: black;
        border-bottom: solid black 1px;
    }

    .style-messagePanel-background{
        @extend .messagePanel-background;
        background-image: url('./Image/sky.jpeg');
        background-repeat: no-repeat;
        background-size: cover;
        color: black;
    }

    .non-style-messagePanel-background{
        @extend .messagePanel-background;
        background-color: white;
        color: black;
    }

    #messagePanel{
        color: black;
        width: 100vw;
        height: calc( 100vh - 100px );
        overflow-y:scroll;
        background-color: rgba(255, 255, 255, 0.6);

        .messageBox{
            margin: 10px;
            margin-left: 100px;
            margin-right: 100px;
            margin-top: 20px;
        }

        .message-box-mine{
            @extend .messageBox;
            float: right;
        }

        .message-box-other{
            @extend .messageBox;
            float: left;
        }

        .account_icon{
            vertical-align: middle;
            margin-right: 10px;
        }

        .style-message{
            vertical-align: middle;
            margin-bottom: 5px;
            margin-top: 2px;
            border-radius: 20px 20px 20px 20px;
            padding: 10px;
            padding-left: 20px;
            padding-right: 20px;
        }

        .style-message-mine{
            //display: inline-block;
            @extend .style-message;
            background-color: rgb(50, 205, 50);
        }
        .style-message-other{
            //display: inline-block;
            @extend .style-message;
            background-color: rgb(255, 255, 255);
        }

        .non-style-message{
            @extend .style-message;
            display: inline-block;
        }
    }

    #messageSender{
        height: 40px;
        vertical-align: middle;
        display: table;

        #msgTextEditInput{
            height: 50px;
        }
        #msgTextEdit{
            width: calc( 100vw - 100px );
            height: 50px;
            display: table-cell;
        }
        .wrapper-div {
            display: table-cell;
            vertical-align: middle;
        }
        #msgSendBtn{
            width: 100px;
            height: 50px;
            background-color: #01579B;
            border-style: none;
        }
        .mdc-text-field__input {
            border-bottom-color: rgba(0, 0, 255, 0.42);
        }
    }
}

.mdc-top-app-bar, .mdc-top-app-bar__row{
    height: 50px;
    background-color: rgba(8, 46, 84, 0.8);
    font-weight: bold;
    padding: 0;
    margin-bottom: 10px;
    max-width: 100vw;
}

.mdc-top-app-bar--fixed-adjust{
    padding-top: 50px;
    max-width: 100vw;
}

#mode-switch-label{
    margin-left: 20px;
    font-weight: bold;
}
